import './index.scss'
import { Switch, Route, useRouteMatch, NavLink } from 'react-router-dom'
import logo from '../../assets/logos/logo.png'
import ForumPage from './pages/Forum'
import OfficialPage from './pages/Official'

const NewsRoute = () => {
  const { path } = useRouteMatch()
  
  return (
    <div className='news-route'>
      <header>
        <img src={logo} alt='logo' />
      </header>
      <nav className='subnavbar'>
        <ul>
          <li><NavLink activeClassName='active' exact to={`${path}`}>Official</NavLink></li>
          <li><NavLink activeClassName='active' to={`${path}/forum`}>Minecraft Forum</NavLink></li>
        </ul>
        <hr />
      </nav>
      <main>
        <Switch>
          <Route exact path={`${path}`}>
            <OfficialPage />
          </Route>
          <Route path={`${path}/forum`}>
            <ForumPage />
          </Route>
        </Switch>
      </main>
    </div>
  )
}

export default NewsRoute
