import { useContext, useState } from "react"
import URls from "../../../../util/URLs"
import { NewsContext } from "../../NewsProvider"

const OfficialPage = () => {
  const news = useContext(NewsContext).filter(n => n.newsType.includes('News page'))

  const [filterText, setFilterText] = useState('')
  const handleFilterTextChange = (value: string) => {
    setFilterText(value)
  }

  return (
    <div>
      <div className="container">
        <form className='filter-form'>
          <div className='form-side'>
            <h3>Search</h3>
            <input type="text" value={filterText} onChange={(e) => handleFilterTextChange(e.target.value)} />
          </div>
          <div className='divider'></div>
          <div className='form-side'>
            <h3>Categories</h3>
            <div className='wrapper'>
            </div>
          </div>
        </form>
      </div>
      <hr />
      {news.map(n =>
        <div
          key={n.id}
          style={{
            display: (filterText === '' || n.title.toLowerCase().includes(filterText) ? 'block' : 'none') 
          }}
        >
          <img src={URls.baseUrl + n.newsPageImage.url} alt="" loading='lazy' />
          <h3>{n.title}</h3>
        </div>
      )}
    </div>
  )
}

export default OfficialPage
