import { useState } from "react"
import RSSFeed from "../../../../util/RSSFeed"

const ForumPage = () => {
  const [feeds] = useState(RSSFeed)

  return (
    <div>
      {feeds.map(feed => (
        <div key={feed.id}>
          <img src={'https://minecraft.net' + feed.image} alt="" />
          <h3>{feed.title}</h3>
          <p>{feed.description}</p>
        </div>
      ))}      
    </div>
  )
}

export default ForumPage
